.contacts {
    background: rgb(255, 255, 255);
    background: linear-gradient(180deg, rgba(255, 255, 255, 1) 15%, rgba(255, 245, 246, 1) 15%);
    padding-bottom: 40px;

    @media screen and (min-width: 768px) {
        background: rgb(255, 255, 255);
        background: linear-gradient(180deg, rgba(255, 255, 255, 1) 38%, rgba(255, 245, 246, 1) 38%);
    }

    @media screen and (min-width: 1200px) {
        background: rgb(255, 255, 255);
        background: linear-gradient(180deg, rgba(255, 255, 255, 1) 10%, rgba(255, 245, 246, 1) 10%);
        padding-bottom: 120px;
    }

}


.contacts-boxes {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-bottom: 32px;

    @media screen and (min-width: 768px) {

        flex-direction: row;
        margin-bottom: 60px;
        gap: 32px;
    }



}

.contacts-cards {
    width: 100%;
    height: 412px;
    background-color: #fff;
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
    align-content: flex-start;
    padding-top: 40px;
    padding-bottom: 40px;
    padding-left: 32px;
    padding-right: 32px;
    display: flex;
    flex-direction: column;

    @media screen and (min-width: 768px) {
        height: 528px;
        width: 224px;



    }

    @media screen and (min-width: 1200px) {
        height: 568px;
        width: 368px;

    }


}

.place-one {
    width: 80px;
    height: 16px;
    background-color: #F0D1A5;
    border-radius: 4px;
    text-align: center;
    justify-content: center;
    margin-bottom: 20px;
    display: flex;
    align-items: center;

    @media screen and (min-width: 768px) {
        height: 28px;
        width: 73px;
        margin-bottom: 32px;


    }

}

.place-two {
    width: 130px;
    height: 16px;
    background-color: #C2E297;
    border-radius: 4px;
    text-align: center;
    justify-content: center;
    margin-bottom: 20px;
    display: flex;
    align-items: center;

    @media screen and (min-width: 768px) {
        height: 28px;
        width: 138px;
        margin-bottom: 32px;

    }
}

.place-three {
    width: 80px;
    height: 16px;
    background-color: #F0D1A5;
    border-radius: 4px;
    text-align: center;
    justify-content: center;
    margin-bottom: 20px;
    display: flex;
    align-items: center;

    @media screen and (min-width: 768px) {
        height: 28px;
        width: 73px;
        margin-bottom: 32px;

    }
}

.contacts-modalbtn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;

    @media screen and (min-width: 768px) {

        flex-direction: row;

        gap: 15px;
    }




}

.modalbtn-one {
    width: 161px;
    height: 44px;
    background: #D41342;
    border-radius: 22px;
    border: none;
    @include font ('DM Sans', 700, 16, 1.31, none, 0.04em);
    color: #fff;

    @media screen and (min-width: 768px) {
        width: 191px;

    }


}


.modalbtn-two {
    width: 161px;
    height: 44px;
    background: #ffffff;
    border-radius: 22px;
    border: none;
    @include font ('DM Sans', 700, 16, 1.31, none, 0.04em);
    color: #D41342;

    @media screen and (min-width: 768px) {
        width: 174px;

    }


}





.contacts-schedule {
    @include font ('DM Sans', 500, 12, 1.83, none, 0.04em);
    color: #fff;

    @media screen and (min-width: 768px) {
        @include font ('DM Sans', 500, 14, 1.64, none, none);
        color: #fff;


    }

    @media screen and (min-width: 1200px) {
        @include font ('DM Sans', 500, 16, 1.64, none, 0.04em);
        color: #fff;


    }
}

.contacts-subtitle,
.contacts-date {
    @include font ('DM Sans', 500, 12, 1.83, none, 0.04em);
    color: black;

    @media screen and (min-width: 768px) {
        @include font ('DM Sans', 500, 14, 1.64, none, none);
        color: black;



    }

    @media screen and (min-width: 1200px) {
        @include font ('DM Sans', 500, 16, 1.87, none, 0.04em);
        color: black;


    }


}

.contacts-text,
.contacts-link {
    @include font ('DM Sans', 500, 12, 1.83, none, 0.04em);
    color: #907E82;

    @media screen and (min-width: 768px) {
        @include font ('DM Sans', 500, 14, 1.64, none, none);
        color: #907E82;


    }

    @media screen and (min-width: 1200px) {
        @include font ('DM Sans', 500, 16, 1.87, none, 0.04em);
        color: #907E82;


    }
}

.contacts-time {
    @include font ('DM Sans', 500, 12, 1.83, none, 0.04em);
    color: #FFA5BA;

    @media screen and (min-width: 768px) {
        @include font ('DM Sans', 500, 14, 1.64, none, none);
        color: #FFA5BA;
        min-width: 153px;

    }

    @media screen and (min-width: 1200px) {
        @include font ('DM Sans', 500, 16, 1.87, none, 0.04em);
        color: #FFA5BA;


    }

}

.contacts-subtitle {
    margin-bottom: 8px;

    @media screen and (min-width: 768px) {
        margin-bottom: 12px;

    }

    @media screen and (min-width: 1200px) {
        margin-bottom: 8px;

    }

}

.contacts-text {
    margin-bottom: 48px;
    position: relative;

    @media screen and (min-width: 768px) {
        margin-bottom: 64px;

    }




}

.wrapper-one {
    margin-bottom: 8px;

    @media screen and (min-width: 768px) {
        margin-bottom: 12px;

    }


}

.wrapper-two {
    margin-bottom: 48px;
    position: relative;

    @media screen and (min-width: 768px) {
        margin-bottom: 64px;

    }

}

.wrapper-two::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    outline: none;

    background-color: #E1E1E1;
    border-radius: 2;


    bottom: -24px;
    left: 0px;






}

.contacts-text::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    outline: none;

    background-color: #E1E1E1;
    border-radius: 2;


    bottom: -24px;
    left: 0px;



}